import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			data: [],
			user_info: '',
		}
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.request({
				url: interfaces.adrList,
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					if (res.code == 200) {
						this.data = res.data;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		} else {
			wx.reLaunch({
				url: '/pages/index/index',
			})
		}
	},

	methods: {
		//编辑门店信息和店长信息
		editStore(){
			wx.navigateTo({
				url: 'editstore',
			})
		},
		//添加员工信息
		addUser() {
			wx.navigateTo({
				url: 'adduser',
			})
		},
		//修改员工信息
		editUser(id) {
			wx.navigateTo({
				url: 'edituser?id=' + id,
			})
		},
	}
}
